const Logo = () => {
  return (
    <StaticImage
    sx = {{width: [ '4em', '6em', '10em' ], m: '3em'}}
      src="../images/logo.png"
      formats={["auto", "webp", "avif"]}
    />
  )
}

const IndexPage = () => {
  const { background } = useStaticQuery(
    graphql`
     query {
       background: file(relativePath: { eq: "bg.png" }) {
         childImageSharp {
            gatsbyImageData(
             width: 600
             placeholder: BLURRED
             formats: [AUTO, WEBP, AVIF]
           )

         }
       }
     }
       `)
  // workaround before gatsby-background-image updates
  // https://github.com/gatsbyjs/gatsby/issues/2470 <- no bg-image
  const imageData = convertToBgImage(getImage(background))
  return (
    <Layout>
      <Seo title="Home" />
      <Hero />
    </Layout>
  )
}

export default IndexPage
